<template>
  <div>
    <h1 class="is-size-1 has-text-centered">Quiz Application</h1>
    <div class="container box">
      <div v-if="!passToTest">
        <h1 class="is-size-3">Lists tests</h1>
        <p v-if="error" class="has-text-danger">{{ error }}</p>
        <div style="padding:10px;margin-left:10px;">
          <div
            style="padding:5px"
            v-for="(test, index) in tests"
            v-bind:key="index"
          >
            <input type="radio" v-model="selectedTest" :value="index" />
            {{ test.name }}
          </div>
        </div>
        <div class="buttons is-centered">
          <button class="button is-primary" @click="startTest()">
            Start Test
          </button>
        </div>
      </div>
      <div v-else>
        <Test :id="selectedTest" :re-start="reStart" />
      </div>
    </div>
  </div>
</template>

<script>
import tests from "./tests";
import Test from "./components/Test";
export default {
  name: "App",
  components: {
    Test,
  },
  data: () => ({
    tests: tests,
    selectedTest: null,
    passToTest: false,
    error: null,
  }),
  created() {},

  methods: {
    startTest() {
      if (this.selectedTest == null) {
        this.error = "Pleae choose a test";
        return;
      }
      this.error = null;
      this.passToTest = true;
    },
    reStart() {
      this.selectedTest = null;
      this.passToTest = false;
    },
  },
};
</script>

