<template>
  <div>
    <div v-if="!finished">
      <h1 class="is-size-3">{{ test.name }}</h1>
      <div style="padding:10px;margin-left:10px;">
        <div class="is-size-5">
          {{ test.questions[questionIndex].question }}
        </div>
        <p v-if="error" class="has-text-danger">{{ error }}</p>
        <div style="padding:10px;margin-left:10px;">
          <div
            v-for="(answer, index) in test.questions[questionIndex].answers"
            v-bind:key="index"
            style="padding:5px"
          >
            <input type="radio" v-model="res" :value="index" />
            {{ answer }}
          </div>
        </div>
      </div>
      <div class="buttons  is-centered">
        <button
          v-if="questionIndex > 0"
          class="button is-danger"
          @click="back()"
        >
          Back
        </button>
        <button
          v-if="test.questions.length - 1 > questionIndex"
          class="button is-success"
          @click="next()"
        >
          Next
        </button>
        <button
          v-if="test.questions.length - 1 == questionIndex"
          class="button is-warning"
          @click="finish()"
        >
          Finish
        </button>
      </div>
    </div>
    <div v-else>
      The Test is
      <span v-if="result" class="has-text-success"> Passed</span
      ><span v-else class="has-text-danger">Faild</span> <br />
      <h1 class="is-size-5">Statistics</h1>
      <div style="margin-left:10px">
        You taked <span class="has-text-info"> {{ timeTaked }} </span> to finish
        th The {{ test.name }}
        <div class="content">
          <ol type="1">
            <li v-for="(val, index) in responces" v-bind:key="index">
              {{ val.question }} is
              <span v-if="val.isCorrect" class="has-text-success"> Correct</span
              ><span v-else class="has-text-danger">Wrong</span>
            </li>
          </ol>
        </div>
      </div>
      <div class="buttons is-centered">
        <button class="button is-primary" @click="reStart()">
          Restart
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import tests from "../tests";
export default {
  name: "Test",
  props: {
    id: Number,
    reStart: Function,
  },
  data: () => ({
    test: null,
    questionIndex: 0,
    finished: false,
    startAt: null,
    finishedAtt: null,
    timeTaked: null,
    res: null,
    responces: [],
    result: null,
    error: null,
  }),
  created() {
    this.test = tests[this.id];
    this.startAt = new Date().getTime();
  },
  methods: {
    start() {},
    finish() {
      if (this.test.questions.length - 1 == this.questionIndex) {
        if (this.res == null) {
          this.error = "choose answers please";
          return;
        }
        this.pushRes();
        this.finished = true;
        this.finishedAtt = new Date().getTime();
        this.timeTaked = new Date(this.finishedAtt - this.startAt)
          .toISOString()
          .substr(11, 8);
        this.result = this.resulta();
      }
    },
    pushRes() {
      this.responces.push({
        question: this.test.questions[this.questionIndex].question,
        res: this.test.questions[this.questionIndex].answers[this.res],
        isCorrect:
          this.test.questions[this.questionIndex].correct_answer == this.res,
      });

      this.res = null;
    },
    next() {
      if (this.test.questions.length - 1 > this.questionIndex) {
        if (this.res == null) {
          this.error = "choose answers please";
          return;
        }
        this.error = null;
        this.pushRes();
        ++this.questionIndex;
      }
    },
    back() {
      if (this.questionIndex > 0) {
        this.error = null;
        this.responces.pop();
        --this.questionIndex;
      }
    },
    resulta() {
      return (
        this.responces.filter((val) => val.isCorrect).length ==
        this.responces.length
      );
    },
  },
};
</script>
